import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BrandPenaltiesComponent } from './brand-penalties.component';

const routes: Routes = [
  {
    path: '',
    component: BrandPenaltiesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BrandPenaltiesRoutingModule { }
