import { DatePipe } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { FormListComponent } from '@fragments';
import { Brand } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { BrandPenalties } from 'app/core/models/brand-penalties.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-brand-penalties',
  templateUrl: './brand-penalties.component.html',
  styleUrls: ['./brand-penalties.component.scss']
})
export class BrandPenaltiesComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent
  dataSource: MatTableDataSource<BrandPenalties>;
  // displayedColumns: string[] = ['brandDescription', 'brandPenaltyAmount', 'deleteIcon'];
  displayedColumns: string[] = ['brandDescription', 'brandPenaltyAmount'];
  brandPenalties = new BrandPenalties();
  brand = new Brand();
  brandPenaltiesList = [];
  brandList = [];
  brandPenaltiesForm: FormGroup;

  constructor(
    private router: Router,
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public datepipe: DatePipe
  ) { }

  ngOnInit(): void {
    this.setFormValidator();
    this.getBrandList();
    this.getBrandPenaltiesList();
  }

  getBrandPenaltiesList(): void {
    this.apiService.findAll(ENDPOINTS.brandPenalties).subscribe(
      (res: any) => {
        if (res) {
          this.brandPenaltiesList = res.responseData.data;
          this.dataSource = new MatTableDataSource<BrandPenalties>(res.responseData.data);
          this.dataSource.paginator = this.formListComponent.paginator;
          this.dataSource.sort = this.formListComponent.sort;
          this.brand = new Brand();
          this.brand = this.brandPenalties.brand;
          if (this.brand) {
            this.brand.penalty = false
            const withPenalty = this.brandPenaltiesList
              .filter(filteredPenalties => filteredPenalties?.brand?.id === this.brand?.id).length === 0;
            if (withPenalty) {
              this.apiService.update(ENDPOINTS.brand, this.brand).subscribe();
            }
          }
        }
      }, err => {
        console.log(err);
      }, () => {
        this.resetFormValidator();
      });
  }

  getBrandPenalties(): void {
    this.apiService.findById(ENDPOINTS.brandPenalties, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.brandPenalties = res.responseData.data;
      }
    });

  }

  getBrandList(): void {
    this.apiService.findAll(ENDPOINTS.activeBrands).subscribe(
      (res: any) => {
        if (res) {
          this.brandList = res.responseData.data;
        }
      });
  }

  save(): void {
    this.formatDate();
    this.apiService.save(ENDPOINTS.brandPenalties, this.brandPenalties).subscribe(
      (res: any) => {
        if (res) {
          this.sweetAlertService.success(res);
          this.router.navigate(['asc/page/maintenance/brand-penalties']);
        } else {
          this.sweetAlertService.error(res);
        }
        this.getBrandPenaltiesList();
      });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.brandPenalties, this.brandPenalties).subscribe(
      (res: any) => {
        if (res) {
          this.sweetAlertService.success(res);
          this.router.navigate(['asc/page/maintenance/brand-penalties']);
        } else {
          this.sweetAlertService.error(res);
        }
        this.getBrandPenaltiesList();
      });
  }

  setFormValidator(): void {
    this.brandPenaltiesForm = this.formBuilder.group({
      brand: ['', Validators.required],
      amount: ['', Validators.required],
      // penaltyDate: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.brandPenalties = new BrandPenalties();
    this.brandPenaltiesForm.markAsUntouched();
    this.brandPenaltiesForm.markAsPristine();
  }

  compareBrandPenalties(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  formatDate(): void {
    // this.brandPenalties.penaltyDate = this.datepipe.transform(new Date(), 'yyyy-MM-dd')
    this.datepipe.transform(this.brandPenalties.penaltyDate, 'yyyy-MM-dd')
  }

  test() {
    alert(this.brandPenalties.penaltyDate)
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this brand penalty?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.brandPenalties, this.brandPenalties.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Brand penalty successfully deleted.');
            this.getBrandList();
            this.getBrandPenaltiesList();
          }
        );
      }
    });
  }

}
